import React, { useState } from "react";
import { useFormik } from "formik";
import { Link, useNavigate } from "react-router-dom";

import axios from "axios";
import * as yup from "yup";
export default function Login({ saveUserdata }) {
  let Navigate = useNavigate();
  let [errorMsg, seterrMsg] = useState("");
  let [loading, setLoading] = useState(false);
  let baseUrl = "https://route-ecommerce.onrender.com";
  let valdition = yup.object({
    email: yup.string().email().required("Email is required"),
    password: yup
      .string()
      .required("Password is required")
      .min(6, "Password is too short - should be 6 chars minimum").matches(/^[A-Z][a-zA-Z0-9!@#$%^&*()_-]{3,16}$/, "enter valid Password"),
  });
  let formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    onSubmit: (x) => {
      sendDatalognin(x);
      console.log(x);
    },
    validationSchema: valdition,
  });
  async function sendDatalognin(values) {
    setLoading(true);
    let { data } = await axios
      .post(`${baseUrl}/api/v1/auth/signin`, values)
      .catch((errorrr) => {
        console.log(errorrr.response.data.message);
        seterrMsg(errorrr.response.data.message);
        setLoading(false);
      });
    setLoading(false);
    console.log(data);
    if (data.message == "success") {
      saveUserdata(data.user);
      Navigate("/home");
      localStorage.setItem("token", data.token);
    }
  }
  return (
    <div className="p-5 shadow my-4 rounded-3">
      <h2 className="text-center">Login Now</h2>
      <form onSubmit={formik.handleSubmit}>
        <div className="my-3">
          <label htmlFor="email">Email :</label>
          <input
            onChange={formik.handleChange}
            type="email"
            className="form-control my-2"
            name="email"
            id="email"
          ></input>
          <p className="text-danger">{formik.errors.email}</p>
        </div>

        <div className="my-3">
          <label htmlFor="password">Password :</label>
          <input
            onChange={formik.handleChange}
            type="password"
            className="form-control my-2"
            name="password"
            id="password"
          ></input>
          <p className="text-danger">{formik.errors.password}</p>
        </div>
        {errorMsg ? <div className="alert alert-danger">{errorMsg}</div> : ""}
        {loading ? (
          <button type="button" className="btn btn-success">
            <i className="fa-solid fa-spinner fa-spin text-white"></i>
          </button>
        ) : (
          <button
            disabled={!formik.isValid}
            type="submit"
            className="btn btn-success"
          >
            Login
          </button>
        )}
<Link to='/frogetPass' className="nav-link mx-3 text-danger fw-bold d-inline-block rounded js-scroll-trigger">Forget Password?</Link>

      </form>
    </div>
  );
}
