import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import $ from "jquery";
import axios from "axios";
export default function Brands() {
  let baseUrl = "https://route-ecommerce.onrender.com";
  let [ProductList, setProducts] = useState([]);
  useEffect(() => {
    getProducts();
  }, []);
  async function getProducts() {
    let { data } = await axios.get(`${baseUrl}/api/v1/brands`);
    setProducts(data.data);
    $(".loading").fadeOut(2000);
  }
  return (
    <div className="row g-3 my-5">
      <div className="position-fixed top-0 end-0 bg-dark start-0 bottom-0 loading">
        <div class="circlewrapper">
          <div class="circlee"></div>
          <div class="circlee"></div>
          <div class="circlee"></div>
          <div class="circleshadow"></div>
          <div class="circleshadow"></div>
          <div class="circleshadow"></div>
        </div>
      </div>
      {ProductList.map((product) => {
        return (
          <>
            <div
              key={product._id}
              className="col-md-3  product position-relative"
            >
              <Link to={"/brandDet/" + product._id}>
                <div className=" card ">
                  <img src={product.image} className="w-100" alt="" />
                  <div className="cardCapition p-3">
                    <h3 className="text-success">{product.name}</h3>
                  </div>
                </div>
              </Link>
            </div>
          </>
        );
      })}
    </div>
  );
}
