import React, { useContext, useEffect, useState } from "react";
import $ from "jquery";
import "./Cart.css";
import { cartContext } from "../../Context/cartContext";
import { toast } from "react-hot-toast";
import { Link } from "react-router-dom";

export default function Cart() {
  let { GetLoogedUserCart, RmoveItem, UpdateItem } = useContext(cartContext);
  let [cartDetials, setcartDetials] = useState(null);
  async function getCart() {
    let data = await GetLoogedUserCart();
    setcartDetials(data.data);
  }
  async function deleteItem(id) {
    let data = await RmoveItem(id);
    setcartDetials(data.data);
    if (data.status == "success") {
      toast.success("remove product success", {
        classNameName: "text-success border-success",
      });
    }
  }
  async function updateproductCount(id, count) {
    let data = await UpdateItem(id, count);
    setcartDetials(data.data);
    if (data.status == "success") {
      toast.success("product Updated success", {
        classNameName: "text-success border-success mt-5",
      });
    }
  }
  useEffect(() => {
    getCart();
  }, []);
  return (
    <>
      {cartDetials !== null ? (
        <div id="w">
          <header id="title">
            <h1>Cart Item</h1>
          </header>
          <div id="page">
            <table id="cart">
              <thead>
                <tr>
                  <th className="first">Photo</th>
                  <th className="second">Qty</th>
                  <th className="third">Product</th>
                  <th className="fourth">Line Total</th>
                </tr>
              </thead>
              <tbody>
                {cartDetials.products.map((product, i) => {
                  return (
                    <tr className="productitm">
                      <td>
                        <img
                          src={product.product.imageCover}
                          height={"150px"}
                          className="thumb"
                        />
                      </td>
                      <td>
                        <div>
                          <button
                            onClick={() =>
                              updateproductCount(
                                product.product._id,
                                product.count + 1
                              )
                            }
                            className="btn btn-outline-success btn-sm"
                          >
                            +
                          </button>
                          <span className="mx-2">{product.count}</span>
                          <button
                            onClick={() =>
                              updateproductCount(
                                product.product._id,
                                product.count - 1
                              )
                            }
                            className="btn btn-outline-success btn-sm"
                          >
                            -
                          </button>
                        </div>
                      </td>
                      <td>
                        {product.product.title.split(" ").slice(0, 2).join(" ")}
                      </td>
                      <td>{product.price}</td>
                      <td>
                        <span
                          className="remove"
                          onClick={() => {
                            deleteItem(product.product._id);
                          }}
                        >
                          <i class="fa-solid fa-trash-can"></i>
                        </span>
                      </td>
                    </tr>
                  );
                })}

                <tr className="totalprice extracosts">
                  <td className="light">Total:</td>
                  <td colspan="2">&nbsp;</td>
                  <td colspan="2">
                    <span className="thick">
                      {cartDetials.totalCartPrice} EGP
                    </span>
                  </td>
                </tr>

                <tr className="checkoutrow">
                  <td colspan="5" className="checkout">
                    <button className="btn btn-outline-success ">
                      <Link
                        to={"/checkout/" + cartDetials._id}
                        className="text-white"
                      >
                        Checkout Now!
                      </Link>
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      ) : (
        ""
      )}
    </>
  );
}
