import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import $ from "jquery";
import { cartContext } from "../../Context/cartContext";
import { toast } from "react-hot-toast";

export default function CategoryProudctsDet() {
  let { addTocart, setnumberCartItem } = useContext(cartContext);

  let { id } = useParams();

  let baseUrl = "https://route-ecommerce.onrender.com";
  let [brandsDet, setBrandsDetail] = useState();
  let [brandsDetarr, setBrandsDetailarr] = useState();

  useEffect(() => {
    getProductDetails();
  }, []);
  async function addproductTocart(id) {
    let data = await addTocart(id);
    if (data.status == "success") {
      setnumberCartItem(data.numOfCartItems);
      toast.success(data.message, {
        className: "text-success border-success mt-5",
      });
    }
  }
  function buttonCheck(btn) {
    let icon = $(btn).children()[0];
    icon.classList.toggle("in-wishlist");
  }
  async function getProductDetails() {
    let { data } = await axios.get(`${baseUrl}/api/v1/products`, {
      params: { category: `${id}` },
    });

    setBrandsDetail(data.data);
    setBrandsDetailarr(data.data.length);
    $(".loading").fadeOut(2000);
  }
  return (
    <>
      {
        <div className="row g-3 my-5">
          <div className="position-fixed top-0 end-0 bg-dark start-0 bottom-0 loading">
            <div class="circlewrapper">
              <div class="circlee"></div>
              <div class="circlee"></div>
              <div class="circlee"></div>
              <div class="circleshadow"></div>
              <div class="circleshadow"></div>
              <div class="circleshadow"></div>
            </div>
          </div>
          {brandsDetarr > 0 ? (
            brandsDet.map((el) => {
              return (
                <div
                  key={el._id}
                  className="col-md-3  product position-relative"
                >
                  <button
                    onClick={() => {
                      buttonCheck(`#${el._id}`);
                    }}
                    id={el._id}
                    className="wrapper position-absolute top-0  end-0"
                  >
                    <div className="icon-wishlist"></div>
                  </button>
                  <button
                    onClick={() => {
                      addproductTocart(`${el._id}`);
                    }}
                    id={el._id}
                    className="wrapperr position-absolute   end-0 "
                  >
                    <i class="cartIcon fa-solid fa-cart-shopping text-danger"></i>
                  </button>
                  <Link to={"/ProductDetails/" + el._id}>
                    <div className=" card ">
                      <img src={el.imageCover} className="w-100" alt="" />
                      <div className="cardCapition p-3">
                        <span className="text-success">{el.category.name}</span>

                        <h2 className="h6 fw-bold">
                          {el.title.split(" ").slice(0, 2).join(" ")}
                        </h2>
                        <div className="d-flex justify-content-between">
                          <p>{el.price}EGP</p>
                          <div className="">
                            <i className="fa-solid fa-star text-warning"></i>
                            {el.ratingsAverage}
                          </div>
                        </div>
                      </div>
                    </div>
                  </Link>
                </div>
              );
            })
          ) : brandsDetarr == 0 ? (
            <div className=" top-0 end-0  start-0 bottom-0 d-flex align-items-center justify-content-center">
              <span className="h1 mx-1">
                Sorry This Category is not available at this time
              </span>
              <i class="fa-solid fa-exclamation text-danger h1"></i>
            </div>
          ) : (
            <></>
          )}
        </div>
      }
    </>
  );
}
