import axios from "axios";
import { createContext, useEffect, useState } from "react";
let baseUrl = "https://route-ecommerce.onrender.com";

export let cartContext = createContext(0);

export default function CartContextProvider(props) {
  let [numberCartItem, setnumberCartItem] = useState(null);
  async function Getcart() {
    let data = await GetLoogedUserCart();

    await setnumberCartItem(data.numOfCartItems);
  }
  useEffect(() => {
    Getcart();
  }, []);
  async function addTocart(productId) {
    let { data } = await axios
      .post(
        `${baseUrl}/api/v1/cart`,
        { productId: productId },
        { headers: { token: localStorage.getItem("token") } }
      )
      .catch((err) => err);
    return data;
  }
  async function GetLoogedUserCart() {
    let { data } = await axios
      .get(
        `${baseUrl}/api/v1/cart`,

        { headers: { token: localStorage.getItem("token") } }
      )
      .catch((err) => err);
    return data;
  }
  async function RmoveItem(productId) {
    let { data } = await axios
      .delete(`${baseUrl}/api/v1/cart/${productId}`, {
        headers: { token: localStorage.getItem("token") },
      })
      .catch((err) => err);
    return data;
  }
  async function UpdateItem(productId, count) {
    let { data } = await axios
      .put(
        `${baseUrl}/api/v1/cart/${productId}`,
        {
          count: count,
        },
        {
          headers: { token: localStorage.getItem("token") },
        }
      )
      .catch((err) => err);
    return data;
  }
  async function onlinePay(cartId, shoppingDet) {
    let { data } = await axios
      .post(
        `${baseUrl}/api/v1/orders/checkout-session/${cartId}?url=http://localhost:3000`,
        {
          details: shoppingDet,
        },
        {
          headers: { token: localStorage.getItem("token") },
        }
      )
      .catch((err) => err);
    return data;
  }
  return (
    <cartContext.Provider
      value={{
        addTocart,
        GetLoogedUserCart,
        RmoveItem,
        UpdateItem,
        onlinePay,
        numberCartItem,
        setnumberCartItem,
      }}
    >
      {props.children}
    </cartContext.Provider>
  );
}
