import React, { useContext } from "react";
import { NavLink } from "react-router-dom";
import logo from "../../assets/freshcart-logo.svg";
import $ from "jquery";
import { cartContext } from "../../Context/cartContext";
export default function Navbar({ userData, logout }) {
  let { numberCartItem } = useContext(cartContext);
  function closeButton() {
    $("#navbarResponsive")[0].classList.toggle("d-block");
  }
  return (
    <nav className="navbar navbar-expand-lg bg-nav fixed-top">
      <div className="container-fluid">
        <img src={logo} alt="logo" />
        <button
          onClick={closeButton}
          id="dropNav"
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-target="#navbarResponsive"
          aria-controls="navbarResponsive"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div
          className="collapse navbar-collapse w-100 justify-content-end"
          id="navbarResponsive"
        >
          {userData ? (
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <NavLink
                  className={({ isActive }) =>
                    isActive ? "nav-link active" : "nav-link"
                  }
                  to="home"
                >
                  Home
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink
                  className={({ isActive }) =>
                    isActive ? "nav-link active" : "nav-link"
                  }
                  to="category"
                >
                  Category
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink
                  className={({ isActive }) =>
                    isActive ? "nav-link active" : "nav-link"
                  }
                  to="brands"
                >
                  Brands
                </NavLink>
              </li>
            </ul>
          ) : (
            ""
          )}

          <ul className="navbar-nav ms-auto mb-2 mb-lg-0 ">
            {userData ? (
              <>
                <li className="nav-item py-2">
                  <i className="fa-brands fa-instgram mx-1"></i>
                  <i className="fa-brands fa-facebook mx-1"></i>
                  <i className="fa-brands fa-tiktok mx-1"></i>
                  <i className="fa-brands fa-twitter mx-1"></i>
                  <i className="fa-brands fa-linkedin mx-1"></i>
                  <i className="fa-brands fa-youtube mx-1"></i>
                </li>
                <li className="nav-item position-relative">
                  <NavLink
                    className={({ isActive }) =>
                      isActive ? "nav-link active" : "nav-link"
                    }
                    to="cart"
                  >
                    <i class="fa-solid fa-cart-shopping fa-lg"></i>

                    <span className="badge text-danger position-absolute top-0 end-0 bg-success text-white p-1">
                      {numberCartItem}
                    </span>
                  </NavLink>
                </li>
                <span className="nav-link logout" onClick={logout}>
                  Logout
                </span>
              </>
            ) : (
              <>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive ? "nav-link active" : "nav-link"
                    }
                    to=""
                  >
                    Login
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive ? "nav-link active" : "nav-link"
                    }
                    to="signin"
                  >
                    SignIn
                  </NavLink>
                </li>
              </>
            )}
          </ul>
        </div>
      </div>
    </nav>
  );
}
