import React, { useState } from "react";
import { useFormik } from "formik";
import { useNavigate } from "react-router-dom";

import axios from "axios";
import * as yup from "yup";
export default function Signin() {
  let Navigate = useNavigate();
  let [errorMsg, seterrMsg] = useState("");
  let [loading, setLoading] = useState(false);
  let baseUrl = "https://route-ecommerce.onrender.com";
  let valdition = yup.object({
    name: yup
      .string()
      .min(2, "Too Short!")
      .max(10, "Too Long!")
      .required("Name is required"),
    email: yup.string().email().required("Email is required"),
    phone: yup
      .string()
      .required("Phone number is required")
      .matches(/^01[0125][0-9]{8}$/gm, "Invalid phone number"),
    password: yup
      .string()
      .required("Password is required")
      .min(6, "Password is too short - should be 6 chars minimum").matches(/^[A-Z][a-zA-Z0-9!@#$%^&*()_-]{3,16}$/, "enter valid Password"),
    rePassword: yup
      .string()
      .required("rePassword is required")
      .oneOf([yup.ref("password")], "rePassword not match"),
  });
  let formik = useFormik({
    initialValues: {
      name: "",
      email: "",
      password: "",
      rePassword: "",
      phone: "",
    },
    onSubmit: (x) => {
      sendDatasignin(x);
      console.log(x);
    },
    validationSchema: valdition,
  });
  async function sendDatasignin(values) {
    setLoading(true);
    let { data } = await axios
      .post(`${baseUrl}/api/v1/auth/signup`, values)
      .catch((errorrr) => {
        seterrMsg(errorrr.response.data.errors.msg);
        setLoading(false);
      });
    setLoading(false);
    console.log(data);
    if (data.message == "success") {
      Navigate("/");
    }
  }
  return (
    <div className="shadow p-4 rounded-3 my-3">
      <h2 className="text-center">Register Now</h2>
      <form onSubmit={formik.handleSubmit}>
        <div className="my-1">
          <label htmlFor="name">Name :</label>
          <input
            onChange={formik.handleChange}
            type="text"
            className="form-control my-1"
            name="name"
            id="name"
          ></input>
          <p className="text-danger">{formik.errors.name}</p>
        </div>
        <div className="my-1">
          <label htmlFor="email">Email :</label>
          <input
            onChange={formik.handleChange}
            type="email"
            className="form-control my-1"
            name="email"
            id="email"
          ></input>
          <p className="text-danger">{formik.errors.email}</p>
        </div>
        <div className="my-1">
          <label htmlFor="phone">Phone :</label>
          <input
            onChange={formik.handleChange}
            type="text"
            className="form-control my-1"
            name="phone"
            id="phone"
          ></input>
          <p className="text-danger">{formik.errors.phone}</p>
        </div>
        <div className="my-1">
          <label htmlFor="password">Password :</label>
          <input
            onChange={formik.handleChange}
            type="password"
            className="form-control my-1"
            name="password"
            id="password"
          ></input>
          <p className="text-danger">{formik.errors.password}</p>
        </div>
        <div className="my-1">
          <label htmlFor="rePassword">rePassword :</label>
          <input
            onChange={formik.handleChange}
            type="password"
            className="form-control my-1"
            name="rePassword"
            id="rePassword"
          ></input>
          <p className="text-danger">{formik.errors.rePassword}</p>
        </div>

        {errorMsg ? <div className="alert alert-danger">{errorMsg}</div> : ""}
        {loading ? (
          <button type="button" className="btn btn-success">
            <i className="fa-solid fa-spinner fa-spin text-white"></i>
          </button>
        ) : (
          <button
            disabled={!formik.isValid}
            type="submit"
            className="btn btn-success"
          >
            Register
          </button>
        )}
      </form>
    </div>
  );
}
