import React, { useEffect, useState } from "react";
import {
  createBrowserRouter,
  RouterProvider,
  Navigate,
  createHashRouter,
} from "react-router-dom";
import jwt_decode from "jwt-decode";
import { Toaster } from "react-hot-toast";
import Home from "../src/compnents/Home/Home.jsx";
import Layout from "../src/compnents/Layout/Layout.jsx";
import Login from "../src/compnents/Login/Login.jsx";
import Signin from "../src/compnents/Signin/Signin.jsx";
import BrandDet from "../src/compnents/Brands/BrandDet.jsx";
import Rrot from "./compnents/Rrot/Rrot.jsx";
import CategoryProudcts from "../src/compnents/CategoryProudcts/CategoryProudcts.jsx";
import ProductDet from "../src/compnents/productDet/producrDet.jsx";
import FrogetPass from "../src/compnents/frogetPassword/frogetPassword.jsx";

import ResetPassword from "./compnents/frogetPassword/ResetPassword.jsx";
import Brands from "./compnents/Brands/Brands.jsx";

import CartContextProvider from "./Context/cartContext.jsx";
import Cart from "./compnents/Cart/Cart.jsx";
import CheckOut from "./compnents/CheckOut/CheckOut.jsx";
import CategoryProudctsDet from "./compnents/CategoryProudctsDet/CategoryProudctsDet.jsx";
import Notfound from "./compnents/Notfound/Notfound.jsx";
export default function App() {
  let [userData, setUserdata] = useState(null);
  useEffect(() => {
    if (localStorage.getItem("token")) {
      let token = localStorage.getItem("token");
      let data = jwt_decode(token);
      setUserdata(data);
    }
  }, []);
  function saveUserdata(data) {
    setUserdata(data);
  }
  function logout() {
    setUserdata(null);
    localStorage.removeItem("token");
    return <Navigate to="/" />;
  }
  function Checkdata(props) {
    if (localStorage.getItem("token")) {
      return <Navigate to="/home" />;
    } else {
      return props.children;
    }
  }
  let routers = createHashRouter([
    {
      path: "",
      element: <Layout userData={userData} logout={logout} />,
      children: [
        {
          index: true,
          element: (
            <Checkdata>
              <Login saveUserdata={saveUserdata} />
            </Checkdata>
          ),
        },
        {
          path: "frogetPass",
          element: <FrogetPass></FrogetPass>,
        },
        {
          path: "*",
          element: <Notfound></Notfound>,
        },
        {
          path: "resetPassword",
          element: <ResetPassword />,
        },

        {
          path: "home",
          element: (
            <Rrot>
              <Home />
            </Rrot>
          ),
        },
        {
          path: "checkout/:id",
          element: (
            <Rrot>
              <CheckOut />
            </Rrot>
          ),
        },
        {
          path: "cart",
          element: (
            <Rrot>
              <Cart />
            </Rrot>
          ),
        },
        {
          path: "ProductDetails/:id",
          element: (
            <Rrot>
              <ProductDet />
            </Rrot>
          ),
        },
        {
          path: "brandDet/:id",
          element: (
            <Rrot>
              <BrandDet />
            </Rrot>
          ),
        },
        {
          path: "CategoryProudctsDet/:id",
          element: (
            <Rrot>
              <CategoryProudctsDet />
            </Rrot>
          ),
        },
        {
          path: "signin",
          element: <Signin />,
        },
        {
          path: "category",
          element: (
            <Rrot>
              <CategoryProudcts />
            </Rrot>
          ),
        },
        {
          path: "brands",
          element: (
            <Rrot>
              <Brands />
            </Rrot>
          ),
        },
      ],
    },
  ]);
  return (
    <CartContextProvider>
      <Toaster />
      <RouterProvider router={routers} />
    </CartContextProvider>
  );
}
