import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import $ from "jquery";

import { useParams } from "react-router-dom";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import { cartContext } from "../../Context/cartContext";
import { toast } from "react-hot-toast";
export default function ProductDet() {
  let { id } = useParams();
  let { addTocart, setnumberCartItem } = useContext(cartContext);
  async function addproductTocart(id) {
    let data = await addTocart(id);
    if (data.status == "success") {
      setnumberCartItem(data.numOfCartItems);
      toast.success(data.message, { className: "text-success border-success" });
    }
  }
  let baseUrl = "https://route-ecommerce.onrender.com";
  let [ProductDetail, setProductDetail] = useState();

  useEffect(() => {
    getProductDetails();
  }, []);
  async function getProductDetails() {
    let { data } = await axios.get(`${baseUrl}/api/v1/products/${id}`);

    setProductDetail(data.data);
    $(".loading").fadeOut(2000);
  }
  return (
    <div>
      <div className="position-fixed top-0 end-0 bg-dark start-0 bottom-0 loading">
        <div class="circlewrapper">
          <div class="circlee"></div>
          <div class="circlee"></div>
          <div class="circlee"></div>
          <div class="circleshadow"></div>
          <div class="circleshadow"></div>
          <div class="circleshadow"></div>
        </div>
      </div>
      {ProductDetail ? (
        <div className="row align-items-center">
          <div className="col-md-4 ">
            <OwlCarousel className="owl-theme" loop items={1}>
              {ProductDetail.images.map((el) => {
                return (
                  <div>
                    <img src={el} />
                  </div>
                );
              })}
            </OwlCarousel>
          </div>
          <div className="col-md-8 ">
            <h2>{ProductDetail.title}</h2>
            <p className="text-muted">{ProductDetail.description}</p>
            <span className="text-success">{ProductDetail.category.name}</span>
            <div className="d-flex justify-content-between">
              <p>{ProductDetail.price}EGP</p>
              <div className="">
                <i className="fa-solid fa-star text-warning"></i>
                {ProductDetail.ratingsAverage}
              </div>
            </div>
            <button
              onClick={() => addproductTocart(ProductDetail._id)}
              className="btn btn-success w-100"
            >
              {" "}
              + Add Cart
            </button>
          </div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
}
