import axios from "axios";
import React, { useEffect, useState } from "react";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import { Link } from "react-router-dom";

export default function Category() {
  let baseUrl = "https://route-ecommerce.onrender.com";
  let [categoriesList, setCategories] = useState([]);

  useEffect(() => {
    getAllProducts();
  }, []);
  async function getAllProducts() {
    let { data } = await axios.get(`${baseUrl}/api/v1/categories`);

    setCategories(data.data);
  }
  return (
    <div className="my-3">
      <OwlCarousel className="owl-theme" dots={false} loop autoplay={true}>
        {categoriesList.map((el) => {
          return (
            <div className="row">
              <div className="col-md-2 col-sm-4">
                <Link to={"/CategoryProudctsDet/" + el._id}>
                  <img height={200} src={el.image} />
                </Link>
              </div>
            </div>
          );
        })}
      </OwlCarousel>
    </div>
  );
}
