import axios from 'axios';
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import * as yup from "yup";

export default function FrogetPass() {
    let [CodeFlag, setCode] = useState(true)
    let [errorMsg, seterrMsg] = useState('')
    let [loading, setLoading] = useState(false);
    let navg = useNavigate()
  let baseUrl = "https://route-ecommerce.onrender.com";
    let valdition =  yup.object({
        email: yup.string().email().required("Email is required"),
      });
    let formik1 = useFormik({
        initialValues: {
          email: "",
        },
        onSubmit: (x) => {
            forgotPasswordsApi(x);
          console.log(x);
        },
        validationSchema: valdition,
    })
    let formik2 = useFormik({
        initialValues: {
            resetCode: ""
        },
        onSubmit: (vals) => {
            console.log(vals);
            resetCodeApi(vals)
        }
    })
    async function forgotPasswordsApi(values) {
        setLoading(true);
        let { data } = await axios
          .post(`${baseUrl}/api/v1/auth/forgotPasswords`, values)
            .catch((errorrr) => {
              
            console.log(errorrr);
            seterrMsg(errorrr.response.data.message);
            setLoading(false);
          });
        setLoading(false);
        console.log(data);
        if (data.statusMsg == "success") {
            setCode(false)
        }
    }
    async function resetCodeApi(valobj) {
        let { data } = await axios.post(`${baseUrl}/api/v1/auth/verifyResetCode`, valobj).catch((erro) => {
            seterrMsg(erro.response.data.message)
        })
        if (data.status == "Success") {
            // Reset
            navg('/resetPassword')

        }
    }
    return (<div className="p-5 shadow my-4 rounded-3">
        <h2>{CodeFlag ?'Send Code To Email :' : 'verification Code :'}</h2>
        {CodeFlag ?
            
          <form onSubmit={formik1.handleSubmit}>
            <div className="my-3">
              <label htmlFor="email">Email :</label>
              <input
                onChange={formik1.handleChange}
                type="email"
                className="form-control my-2"
                name="email"
                id="email"
              ></input>
              <p className="text-danger">{formik1.errors.email}</p>
            </div>
            {errorMsg ? <div className="alert alert-danger">{errorMsg}</div> : ""}
            {loading ? (
              <button type="button" className="btn btn-success">
                <i className="fa-solid fa-spinner fa-spin text-white"></i>
              </button>
            ) : (
              <button
                disabled={!formik1.isValid}
                type="submit"
                className="btn btn-success"
              >
                Login
              </button>
            )}
          </form>
            :
            <form onSubmit={formik2.handleSubmit}>
            <div className="my-3">
              <label htmlFor="resetCode">Reset Code :</label>
              <input
                onChange={formik2.handleChange}
                type="text"
                className="form-control my-2"
                name="resetCode"
                id="resetCode"
              ></input>
              <p className="text-danger">{formik2.errors.resetCode}</p>
            </div>
    
            {errorMsg ? <div className="alert alert-danger">{errorMsg}</div> : ""}
            {loading ? (
              <button type="button" className="btn btn-success">
                <i className="fa-solid fa-spinner fa-spin text-white"></i>
              </button>
            ) : (
              <button
                disabled={!formik2.isValid}
                type="submit"
                className="btn btn-success"
              >
                Login
              </button>
            )}
          </form>}

        </div>
               

      );
}
