import React, { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import toast from "react-hot-toast";
import $ from "jquery";
import axios from "axios";
import MainSlider from "../MainSlider/MainSlider.jsx";
import Category from "../category/category.jsx";
import { cartContext } from "../../Context/cartContext.jsx";

export default function Home() {
  let { addTocart, setnumberCartItem } = useContext(cartContext);
  let baseUrl = "https://route-ecommerce.onrender.com";

  let [ProductList, setProducts] = useState([]);
  useEffect(() => {
    getProducts();
  }, []);
  async function getProducts() {
    let { data } = await axios.get(`${baseUrl}/api/v1/products`);
    setProducts(data.data);
    $(".loading").fadeOut(2000);
  }
  async function addproductTocart(id) {
    let data = await addTocart(id);
    if (data.status == "success") {
      setnumberCartItem(data.numOfCartItems);
      toast.success(data.message, {
        className: "text-success border-success mt-5",
      });
    }
  }
  function buttonCheck(btn) {
    let icon = $(btn).children()[0];
    icon.classList.toggle("in-wishlist");
  }
  return (
    <>
      <div className="position-fixed top-0 end-0 bg-dark start-0 bottom-0 loading">
        <div class="circlewrapper">
          <div class="circlee"></div>
          <div class="circlee"></div>
          <div class="circlee"></div>
          <div class="circleshadow"></div>
          <div class="circleshadow"></div>
          <div class="circleshadow"></div>
        </div>
      </div>

      <MainSlider></MainSlider>
      <Category></Category>
      <div className="row g-3 my-5">
        {ProductList.map((product) => {
          return (
            <div
              key={product._id}
              className="col-md-3  product position-relative"
            >
              <button
                onClick={() => {
                  buttonCheck(`#${product._id}`);
                }}
                id={product._id}
                className="wrapper position-absolute top-0  end-0"
              >
                <div className="icon-wishlist"></div>
              </button>
              <button
                onClick={() => {
                  addproductTocart(`${product._id}`);
                }}
                id={product._id}
                className="wrapperr position-absolute   end-0 "
              >
                <i class="cartIcon fa-solid fa-cart-shopping text-danger"></i>
              </button>
              <Link to={"/ProductDetails/" + product._id}>
                <div className=" card ">
                  <img src={product.imageCover} className="w-100" alt="" />
                  <div className="cardCapition p-3">
                    <span className="text-success">
                      {product.category.name}
                    </span>

                    <h2 className="h6 fw-bold">
                      {product.title.split(" ").slice(0, 2).join(" ")}
                    </h2>
                    <div className="d-flex justify-content-between">
                      <p>{product.price}EGP</p>
                      <div className="">
                        <i className="fa-solid fa-star text-warning"></i>
                        {product.ratingsAverage}
                      </div>
                    </div>
                  </div>
                </div>
              </Link>
            </div>
          );
        })}
      </div>
    </>
  );
}
