import { useFormik } from "formik";
import React, { useContext } from "react";
import * as yup from "yup";
import { cartContext } from "../../Context/cartContext";
import { useParams } from "react-router-dom";

export default function CheckOut() {
  let { id } = useParams();
  let { onlinePay } = useContext(cartContext);
  async function handleSubmit(values) {
    let response = await onlinePay(id, values);
    if (response?.status === "success") {
      window.location.href = response.session.url;
    }
  }
  let valdition = yup.object({
    details: yup.string().required("details is required"),
    city: yup.string().required("city is required"),
    phone: yup
      .string()
      .required("Email is required")
      .matches(/^01[0125][0-9]{8}$/gm, "Invalid phone number"),
  });
  let formik = useFormik({
    initialValues: {
      details: "",
      phone: "",
      city: "",
    },
    onSubmit: handleSubmit,
    validationSchema: valdition,
  });
  return (
    <div className="w-100 my-4 p-5 mx-auto shadow rounded">
      <form onSubmit={formik.handleSubmit}>
        <div className="my-3">
          <label htmlFor="details">details</label>
          <input
            onChange={formik.handleChange}
            type="text"
            name="details"
            id="details"
            className="form-control"
          />
          <p className="text-danger">{formik.errors.details}</p>
        </div>
        <div className="my-3">
          <label htmlFor="phone">phone</label>
          <input
            onChange={formik.handleChange}
            type="text"
            name="phone"
            id="phone"
            className="form-control"
          />
          <p className="text-danger">{formik.errors.phone}</p>
        </div>
        <div className="my-3">
          <label htmlFor="city">city</label>
          <input
            onChange={formik.handleChange}
            type="text"
            name="city"
            id="city"
            className="form-control"
          />
          <p className="text-danger">{formik.errors.city}</p>
        </div>

        <button type="submit" className="btn btn-success w-100">
          pay
        </button>
      </form>
    </div>
  );
}
